@tailwind base;
@tailwind components;
@tailwind utilities;

@layer components {
  .popover-btn {
    @apply w-[39px] h-[39px] p-2.5 ml-2 rounded-full bg-fb-popover flex justify-center items-center cursor-pointer active:bg-fb-active;
  }
  .popover-panel {
    @apply w-[300px] z-50 p-4 bg-fb-header rounded-lg shadow-lg absolute right-0 top-[3.2rem] border border-fb-input lg:w-[360px];
  }
  .card {
    @apply rounded-lg mt-4 px-4 py-3 bg-fb-card;
  }
}

/* 隱藏 scroll-bar */
.invisible-scrollbar::-webkit-scrollbar {
  display: none;
}

.invisible-scrollbar {
  scrollbar-width: none;
}

/* swiper */

.swiper {
  padding-top: 5px !important;
  padding-bottom: 5px !important;
  padding-left: 5px !important;
}

/* 每個 slide 根據內容物寬度自動決定 */
.swiper-slide {
  width: auto !important;
  margin-right: 0 !important;
}

.swiper-button-next {
  background-image: url(https://bruce-fe-fb.web.app/image/right-arrow.svg);
  background-repeat: no-repeat;
  background-size: 24px;
  background-position: center;
  border-radius: 9999px;
  background-color: rgb(115 115 115 / var(--tw-bg-opacity));
  width: 36px !important;
  height: 36px !important;
  top: 29px !important;
}

.swiper-button-next:active {
  background-color: #323436;
}

.swiper-button-next::after {
  display: none;
}

.swiper-button-prev {
  background-image: url(https://bruce-fe-fb.web.app/image/left-arrow.svg);
  background-repeat: no-repeat;
  background-size: 24px;
  background-position: center;
  border-radius: 9999px;
  background-color: rgb(115 115 115 / var(--tw-bg-opacity));
  width: 36px !important;
  height: 36px !important;
  top: 29px !important;
}

.swiper-button-pref:active {
  background-color: #323436;
}

.swiper-button-prev::after {
  display: none;
}

.swiper-button-disabled {
  opacity: 0 !important;
}

/* MeunIcon */
.menu-icon-container {
  display: inline-block;
  cursor: pointer;
}

.menu-icon-bar1,
.menu-icon-bar2,
.menu-icon-bar3 {
  width: 35px;
  height: 5px;
  background-color: #333;
  margin: 6px 0;
  transition: 0.4s;
}

.change .menu-icon-bar1 {
  -webkit-transform: rotate(-45deg) translate(-9px, 6px);
  transform: rotate(-45deg) translate(-9px, 6px);
}

.change .menu-icon-bar2 {
  opacity: 0;
}

.change .menu-icon-bar3 {
  -webkit-transform: rotate(45deg) translate(-8px, -8px);
  transform: rotate(45deg) translate(-8px, -8px);
}
